import axios from 'axios';
import cheerio from 'cheerio';
import hljs from 'highlight.js';

export default {
  async asyncData({ $config }) {
    const categories = await axios.get(
      `https://${$config.serviceId}.microcms.io/api/v1/categories?limit=100`,
      {
        headers: { 'X-API-KEY': $config.apiKey },
      }
    );
    const banner = (
      await axios.get(
        `https://${$config.serviceId}.microcms.io/api/v1/banner`,
        {
          headers: { 'X-API-KEY': $config.apiKey },
        }
      )
    ).data;
    const {
      data: { contents },
    } = await axios.get(
      `https://${$config.serviceId}.microcms.io/api/v1/blog`,
      {
        headers: { 'X-API-KEY': $config.apiKey },
      }
    );
    return {
      categories: categories.data.contents,
      banner,
      contents,
    };
  },
  data() {
    return {
      data: {
        id: '',
        ogimage: {
          url: '',
        },
        body: '',
        title: '',
        createdAt: '',
        publishedAt: '',
        toc_visible: false,
        writer: {
          id: '',
          name: '',
          image: {
            url: '',
          },
          text: '',
        },
        partner: {
          id: '',
          company: '',
          url: '',
          description: '',
          logo: {
            url: '',
          },
        },
        category: {
          name: '',
          color: '',
        },
        related_blogs: [],
      },
      toc: [],
      contents: [],
      categories: [],
    };
  },
  async created() {
    const query = this.$route.query;
    if (query.id === undefined || query.draftKey === undefined) {
      return;
    }
    const { data, error } = await axios
      .get(
        `/.netlify/functions/draft?id=${query.id}&draftKey=${query.draftKey}`
      )
      .catch((error) => ({ error }));
    if (error) {
      return;
    }
    this.data = data;

    // 目次作成
    const $ = cheerio.load(data.body);
    const headings = $('h1, h2, h3').toArray();
    const toc = headings.map((d) => {
      return {
        text: d.children[0].data,
        id: d.attribs.id,
        name: d.name,
      };
    });
    this.toc = toc;
    $('pre code').each((_, elm) => {
      const res = hljs.highlightAuto($(elm).text());
      $(elm).html(res.value);
      $(elm).addClass('hljs');
    });
    this.data.body = $.html();
  },
  head() {
    return {
      title: this.title,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.data && this.data.description,
        },
        {
          hid: 'og:title',
          property: 'og:title',
          content: this.data && this.data.title,
        },
        {
          hid: 'og:description',
          property: 'og:description',
          content: this.data && this.data.description,
        },
        {
          hid: 'og:url',
          property: 'og:url',
          content: `https://blog.microcms.io/${this.data && this.data.id}`,
        },
        {
          hid: 'og:image',
          property: 'og:image',
          content: this.data && this.data.ogimage && this.data.ogimage.url,
        },
      ],
      link: [
        {
          rel: 'stylesheet',
          href:
            'https://cdnjs.cloudflare.com/ajax/libs/highlight.js/9.12.0/styles/monokai-sublime.min.css',
        },
      ],
      script: [
        {
          src:
            'https://cdnjs.cloudflare.com/ajax/libs/highlight.js/9.12.0/highlight.min.js',
          async: true,
        },
      ],
    };
  },
};
